<template>
    <div id="jyqk">
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="就业薪酬排名" name="first" lazy>
                <el-row class="row" :gutter="12">
                    <el-col class="col">
                        <el-card shadow="always">
                            <div class="shaixuan_box">
                                <div style="display:inline-block;">
                                    <span>筛选：</span>
                                    <el-select v-model="value1" placeholder="请选择" style="width:148px;" @change="getValue" size="small">
                                        <el-option
                                        v-for="item in options1"
                                        :key="item.key"
                                        :label="item.label"
                                        :value="item.value">
                                        </el-option>
                                    </el-select>
                                </div>
                            </div>
                            <!-- <div class="cardtitle">就业薪酬排名</div> -->
                            <barCharts v-if="char" :op="baroption1"></barCharts>
                        </el-card>
                    </el-col>
                    <el-col class="coltips">
                        <el-card class="tipheight tip_select" shadow="always">
                            <div class="tips">说明:</div>
                            <p class="tippoint">根据教育部2010年《中等职业学校设置标准(教职成[2010]12号)》文件要求，中职学校占地面积<span style="color:red;">不少于40000m²</span>。</p>
                        </el-card>
                    </el-col>
                </el-row>
            </el-tab-pane>
            <el-tab-pane label="就业率排名" name="second" lazy>
                <el-row class="row" :gutter="12">
                    <el-col class="col">
                        <el-card shadow="always">
                            <div class="shaixuan_box">
                                <div style="display:inline-block;">
                                    <span>筛选：</span>
                                    <el-select v-model="value2" placeholder="请选择" style="width:148px;" @change="getValue" size="small">
                                        <el-option
                                        v-for="item in options2"
                                        :key="item.key"
                                        :label="item.label"
                                        :value="item.value">
                                        </el-option>
                                    </el-select>
                                </div>
                            </div>
                            <!-- <div class="cardtitle">就业率排名</div> -->
                            <barCharts v-if="char" :op="baroption2"></barCharts>
                        </el-card>
                    </el-col>
                    <el-col class="coltips">
                        <el-card class="tipheight tip_select" shadow="always">
                            <div class="tips">说明:</div>
                            <p class="tippoint">根据教育部2010年《中等职业学校设置标准(教职成[2010]12号)》文件要求，中职学校占地面积<span style="color:red;">不少于40000m²</span>。</p>
                        </el-card>
                    </el-col>
                </el-row>
            </el-tab-pane>
            <el-tab-pane label="毕业生满意率排名" name="third" lazy>
                <el-row class="row" :gutter="12">
                    <el-col class="col">
                        <el-card shadow="always">
                            <div class="shaixuan_box">
                                <div style="display:inline-block;">
                                    <span>筛选：</span>
                                    <el-select v-model="value3" placeholder="请选择" style="width:148px;" @change="getValue" size="small">
                                        <el-option
                                        v-for="item in options3"
                                        :key="item.key"
                                        :label="item.label"
                                        :value="item.value">
                                        </el-option>
                                    </el-select>
                                </div>
                            </div>
                            <!-- <div class="cardtitle">毕业生满意率排名</div> -->
                            <barCharts v-if="char" :op="baroption3"></barCharts>
                        </el-card>
                    </el-col>
                    <el-col class="coltips">
                        <el-card class="tipheight tip_select" shadow="always">
                            <div class="tips">说明:</div>
                            <p class="tippoint">根据教育部2010年《中等职业学校设置标准(教职成[2010]12号)》文件要求，中职学校占地面积<span style="color:red;">不少于40000m²</span>。</p>
                        </el-card>
                    </el-col>
                </el-row>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import barCharts from './barCharts'
export default {
    name:'jyqk',
    data(){
        return{
            char:false,
            activeName: 'first',
            baroption1:{
                id: 'chart1',
                title: '就业薪酬排名',
                xAxis: [
                    {
                        data: ['信息工程系', '机电工程汽车系', '人文系', '财贸经济系', '外语系', '规划建筑工程系', '园林与食品工程学院', '互联网学院', '艺术系'],
                        axisLabel: {
                            rotate:-20,
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                    }
                ],
                series: [{
                    name: '人数',
                    type: 'bar',
                    data: [1570, 1300, 1146, 915, 710, 616, 500, 415, 320],
                }]
            },
            baroption2:{
                id: 'chart2',
                title: '就业率排名',
                xAxis: [
                    {
                        data: ['信息工程系', '机电工程汽车系', '人文系', '财贸经济系', '外语系', '规划建筑工程系', '园林与食品工程学院', '互联网学院', '艺术系'],
                        axisLabel: {
                            rotate:-20,
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                    }
                ],
                series: [{
                    name: '人数',
                    type: 'bar',
                    data: [1570, 1300, 1146, 915, 710, 616, 500, 415, 320],
                }]
            },
            baroption3:{
                id: 'chart3',
                title: '毕业生满意率排名',
                xAxis: [
                    {
                        data: ['信息工程系', '机电工程汽车系', '人文系', '财贸经济系', '外语系', '规划建筑工程系', '园林与食品工程学院', '互联网学院', '艺术系'],
                        axisLabel: {
                            rotate:-20,
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                    }
                ],
                series: [{
                    name: '人数',
                    type: 'bar',
                    data: [1570, 1300, 1146, 915, 710, 616, 500, 415, 320],
                }]
            },
            baroption:{
                color: ['#029348'],
                tooltip: {
                    trigger: 'axis',
                    axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                      type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                    }
                  },
                xAxis: [
                    {
                        type : 'category',
                        data: null,
                        axisLabel: {
                            interval:0,
                            rotate:-20,
                            fontSize: 12
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                        type : 'value'
                    }
                ],
                series: [{
                    name: '人数',
                    type: 'bar',
                    data: null,
                    itemStyle: {
                        normal: {
                            label: {
                                show: true, //开启显示
                                position: 'top', //在上方显示
                                textStyle: { //数值样式
                                color: '#029348',
                                fontSize: 14
                                }
                            }
                        }
                    }
                }]
            },
            value1: '全部',
            value2: '全部',
            value3: '全部',
            options1: [{
                key:'1',
                value: ['chart1','-20',['电脑室','化学实验室','图书室','体育室'],[561,245,543,423]],
                label: '全部'
            }, {
                key:'2',
                value: ['chart1','0',['教室','化学室','书本','椅子'],[261,255,343,223]],
                label: '专业'
            }],
            options2: [{
                key:'1',
                value: ['chart2','-20',['电脑室','化学实验室','图书室','体育室'],[561,245,543,423]],
                label: '全部'
            }, {
                key:'2',
                value: ['chart2','0',['教室','化学室','书本','椅子'],[261,255,343,223]],
                label: '专业'
            }],
            options3: [{
                key:'1',
                value: ['chart3','-20',['电脑室','化学实验室','图书室','体育室'],[561,245,543,423]],
                label: '全部'
            }, {
                key:'2',
                value: ['chart3','0',['教室','化学室','书本','椅子'],[261,255,343,223]],
                label: '专业'
            }],
        }
    },
    methods: {
        handleClick(tab, event) {
            // console.log(tab, event);
        },
        getValue(value){
            console.log(value)
            var mychart = this.$echarts.init(document.getElementById(value[0]))
            this.baroption.xAxis[0].axisLabel.rotate = value[1]
            this.baroption.xAxis[0].data = value[2]
            this.baroption.series[0].data = value[3]
            mychart.clear();
            mychart.setOption(this.baroption,true)
        }
    },
    components: {
        barCharts,
    },
       mounted() {
        var _this = this
         _this.chart = true
        $.ajax({
            type: "post",
            url: _this.baseUrl+"/ydjxzg/hx/xsgl.action",
            data: {reg:'zyjyqk',style:'1'},
            dataType: "json",
            success: function (res) {
                if (res.code == 401) {
                    location.href = _this.baseUrl01 
                    console.log('判断进入了401，马上开始跳转');  
                }else if (res.code == 403) {
                    console.log('判断进入了403，马上开始跳转');
                    sessionStorage.setItem("login","nop");
                    // location.href = "http://zg.demo.xqit.net/#/p403"
                    
                    _this.$router.push({
                        path: "/p403m",
                        name: "p403m"
                    })
                }else{
                    console.log(res)
                    if(res.gzypjxc){
                        _this.xData = []
                        _this.yData = []
                    // _this.baroption1.param.push(res.gzypjxc[0].param1)
                        for(var i = 0;i < res.gzypjxc[1].length;i++){
                            _this.xData.push(res.gzypjxc[1][i].mc)
                            _this.yData.push(res.gzypjxc[1][i].zcount)
                        }
                        _this.baroption1.yAxis[0].name = '(元)'
                        _this.baroption1.xAxis[0].data = _this.xData;
                        _this.baroption1.series[0].data = _this.yData;
                    }
                    if(res.zyjylpm){
                        _this.xData = []
                        _this.yData = []
                        //_this.baroption3.param.push(res.zyjylpm[0].param1)
                        for(var i = 0;i < res.zyjylpm[1].length;i++){
                            _this.xData.push(res.zyjylpm[1][i].mc)
                            _this.yData.push(Math.round(res.zyjylpm[1][i].zj*100))
                        }
                        _this.baroption2.yAxis[0].name = '%'
                        _this.baroption2.xAxis[0].data = _this.xData;
                        _this.baroption2.series[0].data = _this.yData;
                    }
                    if(res.mylpm){
                            _this.xData = []
                            _this.yData = []
                            //_this.baroption2.param.push(res.mylpm[0].param1)
                            for(var i = 1;i < res.mylpm.length;i++){
                                _this.xData.push(res.mylpm[i].mc)
                                _this.yData.push(Math.round(res.mylpm[i].zj*100))
                            }
                            _this.baroption3.yAxis[0].name = '%'
                            _this.baroption3.xAxis[0].data = _this.xData
                            _this.baroption3.series[0].data = _this.yData;
                            console.log(_this.baroption3)
                        }
                    _this.char = true
                }
            },
            error:function(err){
                console.log(err)
            }
        });
    },
}
</script>

<style>
#jyqk{
    width: 100%;
    height: 100%;
}
#jyqk .row{
    display:flex;
    justify-content:center;
}
#jyqk .row .col{
    width:750px;
    margin-top:20px;
}
#jyqk .row .cardtitle{
    font-size:25px;
    text-align:center;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
}
#jyqk .row .coltips{
    min-width:280px;
    max-width:300px;
    margin-top:20px;
}
#jyqk .row .coltips2{
    min-width:150px;
    max-width:300px;
    margin-top:20px;
}
#jyqk .shaixuan_box{
    position:relative;
    left:13%;
    bottom:32px;
}
@media all and (max-width: 1200px) {
    #jyqk .shaixuan_box{
        left: 3%!important;
    }
}
#jyqk .row .tipheight{
    height:435px;
}
#jyqk .row .tip_select{
    height:465px;
}
#jyqk .row .tips{
    font-size:25px;
    text-align:left;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
    padding:0 20px;
}
#jyqk .row .tippoint{
    margin-top:50px;
    padding:0 20px;
    line-height:40px;
}
#jyqk .is-active{
    color: #029348 !important;
}
.el-tabs__active-bar{
    background: #029348 !important;
}
#jyqk .is-top :hover{
    color: #029348 !important;
}
#jyqk .is-left :hover{
    color: #029348 !important;
}
#jyqk .el-card{
    padding: 40px 0 40px 0;
}
#jyqk .shaixuan{
    padding: 82px 0 32px 0;
}
.el-tabs__content{
    overflow:inherit;
    padding-bottom: 20px;
}
.el-card{
    transition: 0s;
}
</style>




